@import url(https://fonts.googleapis.com/css2?family=Dancing+Script:wght@700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Comic+Neue:wght@700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=PT+Sans+Narrow:wght@700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Source+Sans+Pro&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Bitter:wght@700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Dancing+Script:wght@700&display=swap);
@import url(https://fonts.googleapis.com/css?family=Liu+Jian+Mao+Cao|Neucha|Patrick+Hand&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Pacifico&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Lobster&display=swap);
body {
  background-image: url(/cursor-react4/static/media/fog.8e1199e9.jpg);
  background-repeat: round;
  background-size: 100%;
}
.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

* {
  padding: 0;
  margin: 0;
}
.menu {
  width: 100%;
  margin-top: 15px;
}
.menu ul {
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  justify-content: space-around;
}
li {
  list-style: none;
  width: 13%;
  height: 35px;
  border-top: 0px;
  text-align: center;
}
.link {
  text-decoration: none;
  color: #006600;
  font-size: 30px;
  font-family: "Dancing Script", cursive;
}
.link:hover {
  color: #0d3300;
  text-shadow: 0.1px 0.1px 0.1px #0d3300, 0 0 1em rgb(12, 122, 27);
}

.contacts {
  width: 25%;
  height: auto;
  background-color: rgba(0, 0, 0, 0.04);
  padding: 20px;
  margin: 5% 5%;
  border-radius: 12px;
}
form {
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  margin-bottom: 40px;
}
.checkbox {
  margin-bottom: 10px;
}
input[type="checkbox"] {
  display: none;
  border: 1px solid #696969;
  margin-right: 15px;
}
.checkbox label:before {
  border-radius: 3px;
  border: 1px solid #696969;
}
.checkbox label {
  margin-right: 20px;
  margin-left: 10px;
  font-size: 25px;
  color: #006600;
  font-family: "Dancing Script", cursive;
}
input[type="checkbox"]:checked + label:before {
  content: "\2713";
  text-shadow: 1px 1px 1px rgba(0, 0, 0, 0.2);
  font-size: 15px;
  color: #006600;
  text-align: center;
  line-height: 25px;
  margin-right: 5px;
}
input {
  outline: none;
  width: 22%;
  height: 30px;
  border-radius: 6.25em;
  margin-bottom: 10px;
  background-color: rgba(0, 0, 0, 0.1);
  border: 0.7px solid #696969;
  position: absolute;
  font-size: 22px;
  color: #006600;
  font-family: "PT Sans Narrow", sans-serif;
  background-image: url(/cursor-react4/static/media/loupe.41d6b62e.png);
  background-size: 20px auto;
  background-repeat: no-repeat;
  background-position: 10px 5px;
  padding-left: 40px;
}
input:hover {
  border: 0.8px solid lightslategray;
}
.contact {
  width: 100%;
  height: 70px;
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
}
#icon {
  width: 18%;
  background-color: #5fc25f;
  border-radius: 100%;
  margin: 7px;
}
#icon img {
  -webkit-filter: invert(88%);
          filter: invert(88%);
}
.inf {
  width: 80%;
}
.inf #name,
.link {
  font-size: 25px;
  color: #003300;
  font-family: "Dancing Script", cursive;
  margin-top: 10px;
}
#phone {
  font-size: 25px;
  color: rgb(15, 19, 22);
  font-family: "Source Sans Pro", sans-serif;
  text-align: start;
}
#iconPhone {
  width: 23px;
  height: auto;
}
#male,
#female {
  width: 50%;
  height: auto;
  margin: 13px;
}
hr {
  border: 0.3px solid grey;
}

.post {
  width: 45%;
  height: 450px;
  display: flex;
  background-color: whitesmoke;
  border: 1px solid #003300;
  border-radius: 12px;
  flex-direction: row;
  flex-wrap: wrap;
  margin-bottom: 100px;
}
.imgPost {
  height: auto;
  margin-left: 50px;
}
.imgPost img {
  width: 85%;
  border-radius: 8%;
  height: 270px;
  margin-top: 10px;
}
.mainInfAutor,
.imgPost {
  width: 90%;
  margin: auto;
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  justify-content: space-around;
}
.author {
  padding-top: 10px;
  margin-left: 5px;
}
.icons {
  width: 90%;
  margin: 10px 70px;
  display: flex;
  flex-direction: row;
  justify-content: space-around;
}
.icons img {
  width: 35px;
  height: auto;
  -webkit-filter: contrast(150%);
          filter: contrast(150%);
}
#photo {
  padding-top: 10px;
  width: 30%;
}
#photo img {
  width: 100%;
  border-radius: 100%;
}
#name,
#msg {
  font-size: 33px;
  font-family: "Patrick Hand", cursive;
}
#nickname,
#data {
  margin-top: 20px;
  font-size: 25px;
  font-family: "Liu Jian Mao Cao";
}
#data {
  font-family: "Neucha", cursive;
}
#msg {
  font-size: 20px;
  padding-top: 10px;
}
.icons span {
  font-size: 25px;
  font-family: "Patrick Hand", cursive;
  margin-right: 70px;
}

.postBlock {
  width: 100%;
  height: auto;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: space-around;
  margin-top: 50px;
}

.photoBlock {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: space-around;
  margin-top: 40px;
}
.vertical {
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
}
.photoBlock img {
  width: 25%;
  height: 400px;
  transition-duration: 0.1s;
}
.photoBlock img:hover {
  transform: scale(1.2);
  box-shadow: 0px 0px 30px #ccc;
  border: 1px solid #003300;
}
.horizont {
  display: flex;
  justify-content: space-between;
}
.horizont img {
  width: 33.3%;
  height: 300px;
}

.inform {
  width: 100%;
  margin-top: 30px;
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  justify-content: space-between;
}
h1 {
  color: #006600;
  font-size: 60px;
  font-family: "Dancing Script", cursive;
  padding-bottom: 50px;
}
.miniInf {
  width: 17%;
  height: auto;
  border: 1px solid #0d3300;
  border-radius: 12px;
  padding: 10px;
  background-color: rgba(245, 245, 245, 0.288);
}
.miniInf p {
  font-family: "Pacifico", cursive;
  color: #0d3300;
  font-size: 20px;
}

.messenger {
  display: flex;
}
.mesage {
  width: 85%;
  margin-top: 5%;
  margin-right: 25px;
  height: auto;
  background-color: rgba(0, 0, 0, 0.04);
  padding: 20px;
  border-radius: 12px;
}
.mesage img {
  width: 15%;
  height: auto;
  border-radius: 100%;
}
.your,
.me {
  width: 100%;
  display: flex;
}
.me {
  margin-left: 230px;
}
.your p,
.me p {
  width: 50%;
  height: auto;
  margin: 20px 10px;
  border: 1px solid #003300;
  background-color: rgba(245, 245, 245, 0.432);
  text-align: center;
  padding-top: 15px;
  border-radius: 32px;
  font-size: 25px;
  color: #003300;
  font-family: "Dancing Script", cursive;
}

